import { Hero } from "./hero.type";
import { Injectable } from "@angular/core";

@Injectable()
export abstract class IHeroService {
  abstract getHeroes(): Hero[];
}

@Injectable()
export class HeroService extends IHeroService {

  getHeroes() {
    return [
      { id: 11, name: 'Mr. Nice' },
      { id: 12, name: 'Narco' },
      { id: 13, name: 'Bombasto' },
      { id: 14, name: 'Celeritas' },
      { id: 15, name: 'Magneta' },
      { id: 16, name: 'RubberMan' },
      { id: 17, name: 'Dynama' },
      { id: 18, name: 'Dr IQ' },
      { id: 19, name: 'Magma' },
      { id: 20, name: 'Tornado' }
    ];
  }
}


